.connect-string-component {
  position: absolute;
  z-index: 1;
  top: 32px;
  right: 20px;
  width: 25%;
  height: auto;
  border: 1px solid black;
  border-radius: 5px;
  background: #d9d9d9;
}

.connect-string-component__label {
  position: relative;
  display: block;
  top: 3px;
  left: 3px;
  width: calc(100% - 5px);
  height: calc(3em + 5px);
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.connect-string-component__connect-string__input {
  position: relative;
  top: 3px;
  left: 0px;
  width: calc(100% - 7px);
  height: calc(1em + 3px);
  padding: 2px 2px 2px 2px;
  background: #f5f5f5;
  border: solid 1px black;
  border-radius: 3px;
}



